import React from 'react'

const Party = props => (
    props.list.map(row =>(
        <tr key={row.id}>
            <td>{row.IPv4}</td>
            <td>{row.Fecha}</td>
            <td>{row.Hours}</td>
            <td>{row.Account}</td>
            <td>{row.Character}</td>
            <td>{row.Party}</td>
        </tr>
    ))
)

export default Party


