import React from 'react'
import {
	BrowserRouter as Router,
	Link
} from 'react-router-dom'

const MenuPrincipal = props => (
  <Router>
    <nav>
      <ul className="ed-menu l-horizontal button-bar">
        <li><Link to="/">Home</Link></li>
      </ul>
    </nav>
  </Router>
)

export default MenuPrincipal


