import React from 'react';
import ReactFileReader from 'react-file-reader'
import { MenuPrincipal, GameInfoSearch } from './index'
import '../assets/css/Header.css'
import CyborgPlay from '../assets/img/cyborgplay-logo.svg'

const Header = props => (
    <div className="main-distribute main-menu cross-center header">
        <img src={CyborgPlay} alt="Logo CyborgPlay" className="header-img"/>
        <ReactFileReader fileTypes={[".txt"]} handleFiles={props.OnAddFiles} multipleFiles={false} >
            <button>Update</button>
        </ReactFileReader>
        <GameInfoSearch onSearch={props.onSearch}/>
        <MenuPrincipal />
    </div>
)

export default Header



