import moment from 'moment'

const patterns = data => {
    const _IPv4 = /(([1-9][0-9][0-9]|[1-9][0-9]|[1-9])\.([1-9][0-9][0-9]|[1-9][0-9]|[1-9])\.([1-9][0-9][0-9]|[1-9][0-9]|[1-9])\.([1-9][0-9][0-9]|[1-9][0-9]|[1-9]))/
    const _Fecha = /[0-1][0-9]\/[0-3][0-9]/
    const _Hours = /[0-2][0-9]:[0-5][0-9]:[0-5][0-9]\.[0-9][0-9][0-9]/
    const _Account = /\(\*(([a-z]?)+)?(([0-9_]?)+)?(([a-z]?)+)\)/i
    const _Character = /(([a-z]?)+)?(([0-9_-]?)+)?(([a-z]?)+)\(/i
    const _Party = /'(((([a-z]?[0-9]?)+)([ ]+)?)+)?'/i
    let id= 0
    return data.map(row => ({
            id: id++,
            IPv4: (`${row.match(_IPv4)}`).split(',')[0],
            Fecha: (`${moment().format('YYYY')}-${row.match( _Fecha )}`).replace('/', '-'),
            Hours: (`${row.match(_Hours)}`).split(',')[0],
            Account: (`${row.match(_Account)}`).split(',')[0].slice(2, -1),
            Character: (`${row.match(_Character)}`).split(',')[0].slice(0, -1),
            Party: (`${row.match(_Party)}`).split(',')[0].slice(1, -1)
        })
    )
}

export default patterns


