import React, { Component } from 'react'
import '../assets/css/TableScaner.css'

import Party from './Party'

class TableScaner extends Component {
    constructor(...props){
        super(...props)
        this.state = {
            paginaActual: 1,
            registros: 50
        }

        this.handleOnPagination = this.handleOnPagination.bind(this)
        this.nextPage = this.nextPage.bind(this)
        this.prevPage = this.prevPage.bind(this)
    }

    handleOnPagination(data){
        let inicio = this.state.registros * this.state.paginaActual - this.state.registros,
            final = inicio + this.state.registros
        let MaxPage = Math.ceil(data.length / this.state.registros)
        let datos = [{id: 0, IPv4: 'No hay datos', Fecha: 'No hay datos', Hours: 'No hay datos', Account: 'No hay datos', Character: 'No hay datos', Party: 'No hay datos'}]
        if (this.state.paginaActual <= MaxPage ){
            datos.shift()
            for(let i = inicio; i < final && i < data.length; i++){
                datos.push(this.props.data[i])
            }
        }
        return datos
    }

    nextPage(){
        (this.state.paginaActual < this.state.MaxPage)
        ? this.setState({ paginaActual: this.paginaActual + 1 })
        : false
        this.handleOnPagination(this.props.data)
    }

    prevPage(){
        (this.paginaActual < 0)
        ? this.setState({ paginaActual: this.paginaActual - 1 })
        : false
        this.handleOnPagination(this.props.data)
    }

    render(){
        return(
            <div>
                <table className="TableScaner" >
                    <thead>
                        <tr>
                            <th>IPv4</th>
                            <th>Fecha</th> 
                            <th>Hora</th>
                            <th>Cuenta</th>
                            <th>Personaje</th>
                            <th>Partida</th>
                        </tr>
                    </thead>
                    <tbody>
                        <Party list={this.handleOnPagination(this.props.data)} />
                    </tbody>
                </table>
            </div>
        )
    }
}

export default TableScaner


