import React from 'react'

const gameInfoSearch = props => (
  <form className="formSearch">
    <input type="search" id="search" name="search" placeholder="Cuenta, Personaje, IPv4" className="formSearch-input" onChange={props.onSearch}/>
    <label htmlFor="search" className="formSearch-label button">
      <i className="fa fa-search"></i>
    </label>
  </form>
)

export default gameInfoSearch


