import React, { Component } from 'react'

import 'ed-grid/css/ed-grid.min.css'

import Header from '../components/Header'
import patterns from '../helpers/patterns'
import TableScaner from '../components/TableScaner'
import Footer from '../components/Footer'

// import Home from './Home'
// import Error404 from './Error404'

class App extends Component {
    constructor(){
      super()
      this.state = {        
          gameInfo: [],
          filter: {
            gameInfo: [],
            IPv4: [],
            Account: [],
            Character: [],
            search: ''
          }
      }
      this.handleOnAddFiles = this.handleOnAddFiles.bind(this)
      this.handleOnSearch = this.handleOnSearch.bind(this)
      this.handleOnFilter = this.handleOnFilter.bind(this)
    }

    handleOnSearch(e) {
      let newFilter = Object.assign( {}, this.state.filter, { [e.target.name]: [e.target.value] } )
      this.setState({
        filter: newFilter
      })
    }

    handleOnFilter(filter, data) {
      let regex = new RegExp(filter.search, 'i')
      return data.filter(q => ( regex.test(q.IPv4) || regex.test(q.Account) || regex.test(q.Character) ))
    }

    handleOnAddFiles(fileList) {
      let reader = new FileReader();
      let file = fileList.item(0)
      reader.readAsText(file);
      reader.onload = e => (e.target.readyState === FileReader.DONE )
        ?
          this.setState( { gameInfo: patterns(e.target.result.split('\n')) })
        : 
          console.log('Error al cargar el archivo')
    }

    render() {
      return (
        <div className="App-container flex-column">
            <Header OnAddFiles={this.handleOnAddFiles} onSearch={this.handleOnSearch}/>
            <TableScaner data={this.handleOnFilter(this.state.filter, this.state.gameInfo)}/>
            <Footer classNames="footer" />
        </div>
      );
    }
  }
  
  export default App;



